export default {
	data() {
		return {
			user: '',
			psw: '',
			token:'',
			mon:'none'
		};
	},
	onLoad(e) {
		if(e.user){
			this.user = e.user;
			uni.showToast({
				title:'注册成功请登录',
				icon:'none',
				duration:2000
			})
		}else if(e.cgpwd){
			this.user = e.cgpwd;
			uni.showToast({
				title:'密码已修改，请重新登录',
				icon:'none',
				duration:2000
			})
		}else if(e.name){
			this.user = e.name;
			uni.showToast({
				title:'token过期，请重新登录',
				icon:'none',
				duration:2000
			})
		}
	},
	methods: {
		//登录
		login: function() {
			if (this.user && this.psw) {
				uni.request({
					url: this.serverUrl + '/signin/match',
					data: {
						data:this.user,
						pwd:this.psw
					},
					method:'POST',
					success:(data) => {
						let status = data.data.status;
						if(status == 200){
							this.mon = 'none';
							// 登录成功
							let res = data.data.back;
							console.log(res)
							// 本地存储用户信息
							try{
								uni.setStorageSync('user',{'token':res.token,'id':res.id,'name':res.name,'imgurl':res.imgurl})
							}catch(e){
								console.log('数据库存储错误')
							}
							uni.navigateTo({
								url:'../index/index'
							})
						}else if(status == 500){
							uni.showToast({
								title:'服务器出错！',
								icon:'none',
								duration:2000
							})
						}else{
							this.mon = 'block';
							this.psw = ''
						}
					}
				});
			}
		},
		//跳转到注册界面
		toSignUp: function() {
			uni.navigateTo({
				url: '../signup/signup'
			});
		},
		backOne:function(){
			uni.navigateBack({
				delta:1
			})
		}
	}
};
